import React, { useEffect, useState } from 'react';

const Reference = () => {
  const references = [
    "1. . 2023. , 13 June 2024, http://sthjj.ningbo.gov.cn/art/2024/6/13/art_1229051263_58910489.html",
    "2. . 2023 , 16 Oct. 2023, http://sthjj.ningbo.gov.cn/art/2023/10/16/art_1229051372_58909758.html",
    "3. . 202411. , 13 Jan. 2025, http://sthjj.ningbo.gov.cn/art/2025/1/13/art_1229051286_58911188.html",
    "4. PM2.5AQI. , https://aqicn.org/city/ningbo/cn/",
    "5. . . , https://air.znbeemc.cn/",
    "6. . RWKV. , https://www.baike.com/wikiid/7211023479205462072?baike_source=doubao."
  ];

  // 
  const fullText = references.join('\n');

  const [displayed, setDisplayed] = useState('');

  useEffect(() => {
    let i = 0;
    const interval = setInterval(() => {
      setDisplayed(fullText.slice(0, i));
      i++;
      if (i > fullText.length) clearInterval(interval);
    }, 20);
    return () => clearInterval(interval);
  }, [fullText]); //   fullText 

  return (
    <div style={{
      background: '#1c1c1c',
      border: '1px solid #333',
      borderRadius: '12px',
      padding: '1.5rem',
      margin: '3rem auto',
      maxWidth: '800px',
      color: '#ccc',
      fontSize: '0.85em',
      lineHeight: '1.6',
      fontFamily: 'monospace',
      whiteSpace: 'pre-wrap',
      boxShadow: '0 0 12px rgba(255, 255, 255, 0.05)'
    }}>
      {displayed}
    </div>
  );
};

export default Reference;