import React from 'react';
import { motion } from 'framer-motion';

const BookFlipIntro = () => {
  const pages = [
    {
      title: 'PM2.5 ',
      text: 'PM2.5 2.5'
    },
    {
      title: '',
      text: ''
    },
    {
      title: '',
      text: '2016PM2.5'
    }
  ];

  return (
    <motion.section
      initial={{ opacity: 0 }}
      whileInView={{ opacity: 1 }}
      transition={{ duration: 1 }}
      viewport={{ once: true }}
      style={{
        maxWidth: '800px',
        margin: '4rem auto',
        padding: '2rem',
        borderRadius: '12px',
        background: '#151515',
        color: '#ccc',
        boxShadow: '0 0 10px #000'
      }}
    >
      <h2 style={{ color: '#e6374e', marginBottom: '1rem', textAlign: 'center' }}>  </h2>
      {pages.map((p, i) => (
        <motion.div
          key={i}
          initial={{ rotateY: 90, opacity: 0 }}
          whileInView={{ rotateY: 0, opacity: 1 }}
          transition={{ duration: 0.6, delay: i * 0.4 }}
          style={{
            background: '#222',
            borderRadius: '8px',
            padding: '1.5rem',
            marginBottom: '1.5rem',
            transformStyle: 'preserve-3d'
          }}
        >
          <h4 style={{ color: '#ff90b3', marginBottom: '0.5rem' }}>{p.title}</h4>
          <p style={{ fontSize: '0.95rem' }}>{p.text}</p>
        </motion.div>
      ))}
    </motion.section>
  );
};

export default BookFlipIntro;